<template>
  <div class="ai-charts-module" :class="currentTheme">
    <div class="ai-charts-module-content" ref="aiCustomCharts" v-show="!isEmptyData"></div>
    <div class="ai-charts-module-empty-content" v-show="isEmptyData">
      {{tipsStatus}}
    </div>
  </div>
</template>
<script>
import echarts from 'echarts'
import ElementResizeDetectorMaker from 'element-resize-detector'

export default {
  props: {
    data: {
      type: Array,
      default: function () {
        return []
      }
    },
    config: {
      type: Object,
      default: function () {
        return {}
      }
    },
    theme: {
      type: String,
      default: ''
    },
  },
  data () {
    return {
      isEmptyData: false,
      currentTheme: [],
      //,,....
      tipsStatus: ''
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.init();
    })
  },
  methods: {
    // 
    init () {
      // 
      if (!this.chart) {
        let el = this.$refs.aiCustomCharts;
        console.log(el);
        el && (this.chart = echarts.init(el));
      }

      if (!this.chart) { return; }

      // 
      this.currentTheme = [`theme-${this.theme}`];

      // this.resize();
      // 
      this.createChart();
      // elresize
      let elementResizeDetector = ElementResizeDetectorMaker({
        strategy: 'scroll',  // 
        callOnAdd: false     // , true
      });
      // 
      elementResizeDetector.listenTo(this.$el, () => {
        // 
        this.resize(this.$el.clientWidth, this.$el.clientHeight)
      });
    },
    // 
    draw (options) {
      this.chart.setOption(options || {});
    },
    // 
    resize (width = this.$el.clientWidth, height = this.$el.clientHeight) {
      console.info(width, height)
      this.chart.resize(width, height);
    }
  }
}
</script>