import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import main from '@/views/main.vue'

// 
const chalk = () =>
    import ('./views/theme/theme-chalk.vue')
    // 
const mixiaoku = () =>
    import ('./views/theme/theme-mixiaoku.vue')

const EMPTY_VIEW = { template: '<router-view />' };

let children = [{
        path: '/',
        redirect: 'basic'
    },
    {
        path: 'basic',
        component: EMPTY_VIEW,
        meta: { name: '' },
        children: [{
                path: '/',
                redirect: 'radio'
            },
            {
                path: 'radio',
                meta: { name: 'radio ' },
                component: () =>
                    import ('@/views/basic/radio/index'),
            },
            {
                path: 'statistic',
                meta: { name: 'statistic ' },
                component: () =>
                    import ('@/views/basic/statistic/index'),
            }
        ]
    },
    {
        path: 'chart',
        component: EMPTY_VIEW,
        meta: { name: '' },
        children: [{
                path: '/',
                redirect: 'line'
            },
            {
                path: 'line',
                meta: { name: 'line ' },
                component: () =>
                    import ('@/views/charts/line'),
                children: [{
                    path: 'line-a',
                    mata: { name: '-a' },
                    component: () => {
                        import ('@/views/charts/line')
                    }
                }]
            },
            {
                path: 'pie',
                meta: { name: 'pie ' },
                component: () =>
                    import ('@/views/charts/pie'),
            },
            {
                path: 'scatter',
                meta: { name: 'scatter ' },
                component: () =>
                    import ('@/views/charts/scatter'),
            }
        ]
    },
    {
        path: 'template',
        component: EMPTY_VIEW,
        meta: { name: '' },
    }
]

export default new Router({
    routes: [{
        path: '/',
        meta: { name: '/' },
        component: EMPTY_VIEW,
        children: [{
                path: '/',
                redirect: '/main' // 
            },
            {
                path: '/main',
                meta: { name: 'main' },
                component: main,
                children: [{
                        path: '/',
                        redirect: 'theme-chalk'
                    },
                    {
                        path: 'theme-chalk',
                        name: 'theme-chalk',
                        component: chalk,
                        meta: { name: ': ' },
                        children: children
                    },
                    {
                        path: 'theme-mixiaoku',
                        name: 'theme-mixiaoku',
                        component: mixiaoku,
                        meta: { name: ': ' },
                        children: children
                    }
                ]
            },
        ]
    }]
});