export class CustomHandle {
    constructor(data, config) {
        this.data = data || [];
        this.config = config || {};
        this.option = {};
    };
    // 
    init() {
        this.setTitle();
        this.setXAxis();
        this.setYAxis();
        this.setSeries();
        console.log(this.option);
    };
    // 
    setTitle() {
        //  https://echarts.baidu.com/option.html#title
        let title = {
            text: '',
            top: '15',
            left: 'center',
            textStyle: {
                color: '#ccc'
            }
        };
        this.option.title = Object.assign(title, this.config.title || {});
    };
    // X
    setXAxis() {
        //  https://www.echartsjs.com/option.html#xAxis
        let xAxis = {
            type: 'category',
            data: []
        };
        let t = this.data[0].data;
        t.forEach(item => {
            xAxis.data.push(item.name);
        });
        this.option.xAxis = {};
    };
    // Y
    setYAxis() {
        //  https://www.echartsjs.com/option.html#yAxis
        let yAxis = {
            type: 'value'
        };
        this.option.yAxis = {};
    };
    // 
    setSeries() {
        //  https://www.echartsjs.com/option.html#series
        let series = [];

        this.data.forEach(item => {
            let t = {
                type: 'scatter',
                symbolSize: 20,
            };

            Object.assign(t, this.config.series || {});
            t.name = item.name;
            t.data = [];
            item.data.forEach(childItem => {
                t.data.push(childItem.value);
            });
            series.push(t);
        });
        this.option.series = series;
    };
}