<script>
import ParentCharts from './../_echarts';
import { CustomHandle } from './proces.js';

export default {
    extends: ParentCharts,
    methods: {
        // 
        createChart () {
            if (Array.isArray(this.data)) {
                let ch = new CustomHandle(this.data, this.config);
                // ...
                ch.init();
                // 
                this.draw(ch.option);
            } else {
                this.isEmptyData = true;
            }
        },
    }
}
</script>