<template>
	<!--  () |  () -->
	<div class="ai-radio" :class="[theme,customConfig.customClass]">
		<label role="radio" @keydown.space.stop.prevent="model = isDisabled ? model : label">
			<input
				type="radio"
				name="name || 'radio_' + radioVal"
				:disabled="disabled"
				:checked="label == model ? 'true' : ''"
				@change="change"
			>
			<slot></slot>
		</label>
	</div>
</template>
<script>
	import parent from "@/components/basic/basic";

	export default {
		extends: parent,
		name: "ai-radio",
		props: {
			label: {
				type: [String, Number, Boolean],
				default: ""
			},
			disabled: {
				type: Boolean,
				default: false
            },
            /**
             * 
             * 
             * 
             */
            options: {
			    type: Object,
                default: function() {
                    return {
                        xxx: 'xxx'
                    }
                }
            }
		},
		computed: {
			//  props  --> !!!
            customConfig: function() {
                //  props.options 
                let c = this.$options.__proto__.props.options.default();
                //  props.options 
                let p = this.$options.extends.props.options.default();
                //    props  props 
                return Object.assign(p, c, this.options);
            },
			model: {
				get() {
					return typeof this.value !== "undefined" ? this.value : "";
				},
				set() {}
			}
		},
		watch: {},
		data() {
			return {};
		},
		methods: {
			change() {
				this.$emit("input", this.label);
			},
			clear() {
			    // , , 
            },
			load() {
				// 
                this.model = this.value !== "undefined" ? this.value : "";
			}
		},
		mounted() {
			this.load()
		},
		destroyed() {
		    this.clear()
        }
	};
</script>
<style>

	.ai-radio {
		display: inline-block;
		margin-right: 15px;
	}

	.ai-radio:last-child {
		margin-right: 0;
	}

	/*    */
	.ai-radio.theme-chalk {

